*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }

body {
  font-family: "Nunito", sans-serif;
  font-weight: 300;
  line-height: 1.6; }

.container {
  display: grid;
  grid-template-rows: [navbar-start] 8rem [navbar-end header-start] min-content [header-end content-start] min-content [content-end footer-start] min-content [footer-end];
  grid-template-columns: [full-start navbar-start] minmax(6rem, 1fr) [center-start] repeat(3, minmax(min-content, 40rem)) [center-end] minmax(6rem, 1fr) [full-end navbar-end]; }

.header-1 {
  font-size: 8rem; }

.header-3 {
  font-size: 3rem;
  letter-spacing: 1.5px; }

p {
  font-size: 2rem;
  word-spacing: 3px; }

.navbar {
  grid-column: full-start / full-end;
  grid-row: navbar-start / navbar-end;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center; }

.logo-box {
  grid-column: 1 / span 1;
  padding: 2rem; }

.nav-list {
  grid-column: 2 / span 2;
  justify-self: flex-end;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  column-gap: 5rem;
  margin-right: 10rem; }
  .nav-list__item {
    text-transform: uppercase;
    list-style: none;
    font-size: 1.5rem;
    font-weight: bold;
    transition: all 0.2s; }
    .nav-list__item--link {
      text-decoration: none;
      color: #aaaaaa;
      transition: all 0.2s ease-in; }
  .nav-list__item:hover {
    transform: scale(1.2) rotate(-10deg); }

.header {
  grid-column: center-start 1 / center-end 3;
  grid-row: header-start / header-end;
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  column-gap: 2rem; }

.text-section {
  grid-column: 1 / span 1;
  line-height: 1.2;
  display: grid;
  row-gap: 2rem; }

.avatar-section {
  grid-column: 2 / span 2; }

.content {
  grid-column: center-start 1 / center-end 3;
  grid-row: content-start / content-end; }

.footer {
  grid-column: full-start / full-end;
  grid-row: footer-start / footer-end; }
